import wx from 'weixin-js-sdk';

let recordTimer = 0;

export default {

  mounted() {
    // this.$router.push({
    //   name: 'dataReport',
    //   params: {
    //     voice: ''
    //   }
    // });
  },

  data(){
    return {
      recording: false,
      pending: false
    }
  },

  computed: {
    primaryTip() {
      if (this.pending && !this.recording) {
        return '...';
      } else if (this.recording) {
        return '...';
      } else {
        return '';
      }
    },

    secondaryTip() {
      if (this.pending && !this.recording) {
        return '';
      } else if (this.recording) {
        return '';
      } else {
        return '';
      }
    }
  },

  methods: {
    // /
    handleToggleRecord() {
      if (this.recording) {
        this.stopRecord();
      } else {
        // 
        wx.startRecord({
          cancel: () => {
            this.$vux.toast.show({
              type: 'warn',
              text: ''
            });
          }
        });

        this.recording = true;
        // 10
        recordTimer = setTimeout(() => {
          this.stopRecord();
        }, 10000);
      }
    },

    // 
    stopRecord(){
      // 
      clearTimeout(recordTimer);
      // sdk
      wx.stopRecord({
        success: res => {
          const localId = res.localId;

          this.pending = true;

          wx.translateVoice({
            localId,
            isShowProgressTips: 0,
            complete: res => {
              if (res.hasOwnProperty('translateResult')) {
                this.pending = false;

                this.$router.push({
                  name: 'dataReport',
                  params: {
                    voice: res.translateResult
                  }
                });
                
              } else {
                this.pending = false;
                this.$vux.toast.show({
                  type: 'warn',
                  text: ''
                });
              }
            }
          })
        }
      });

      this.recording = false;
    }
  }
}



