// require('es6-promise').polyfill();
// import fetch from 'isomorphic-fetch';

import axios from 'axios';


// 
const getChartData = (lang_text, prevData, succ, fail) => {
  prevData = prevData || {};

  axios.get(`https://dmp-dbeta.mypaas.com.cn/api/quick_insight/text?t=${new Date().getTime()}`, {
    params: {
      ...prevData,
      lang_text: lang_text, 
      tenant_code: 'beta'
    }
  })
  .then(res => {
    typeof succ === 'function' && succ(res.data);
  })
  .catch(err => {
    console.log('Get chart data api failed: ', err);
    typeof fail === 'function' && fail(err);
  })
}

// JSSDK
const getSignature = (succ, fail) => {
  axios.post('https://dmp-dbeta.mypaas.com.cn/api/quick_insight/signature?t=' + new Date().getTime())
  .then(res => {
    typeof succ === 'function' && succ(res.data);
  })
  .catch(err => {
    console.log('Get wexin signature failed: ', err);
    typeof fail === 'function' && fail(err);
  })
}

export {
  getChartData,
  getSignature
}


