//  echarts  . 
// null ->  '-'
// 12.000002121 -> 2
export default function fmtSeries(data) {
  let _data = data
  if (!isNaN(+data)) {
    if (data != null && data !== '') {
      // 
      if (data.toString().indexOf('.') > -1) {        
        _data = Number(+data).toFixed(2)
      }
    } else {
      _data = '-'
    }
  } else {
    _data = '-'
  }
  return _data
}


