import wx from 'weixin-js-sdk';

import {getSignature} from './apis';

export default {
  name: 'index',

  mounted() {
    // test 
    this.loaded = true;

    // 
    getSignature(json => {
      console.log('getSignature', json);
      // 
      // {
      //   "timestamp": 1498916939,
      //   "nonceStr": "JM9ya8Tujs647XL1",
      //   "appId": "wx7bc82d063226059e",
      //   "signature": "3743dafb6aaa8492c0ce5756cd00f9aa97fe0e23"
      // }
      if (json.result) {
        wx.config({
          timestamp: json.data.timestamp,
          nonceStr: json.data.nonceStr,
          appId: json.data.appId,
          signature: json.data.signature,
          debug: false,
          jsApiList: [
            'startRecord',
            'stopRecord',
            'translateVoice'
          ]
        });
      }
    });

    // wx sdk
    wx.ready(() => {
      this.loaded = true;
      console.log('wx-js-sdk is ready!!');

      if (!localStorage.getItem('has-record-auth')) {
        //  
        wx.startRecord({
          cancel: () => {
            wx.stopRecord();
          }
        });
        setTimeout(() => {
          wx.stopRecord({
            success: () => {
              localStorage.setItem('has-record-auth', 1);
            }
          });
        }, 10)
        wx.stopRecord({
          success: () => {
            localStorage.setItem('has-record-auth', 1);
          }
        });
      }
      
    });
  },

  data () {
    return {
      loaded: false,
      transitionName: 'slide-left'
    }
  },

  watch: {
    '$route' (to, from) {
      this.transitionName = calcTransition(to, from);
    }
  }

};

let _pathHistory = [];
//  slide-left     slide-right
function calcTransition(to, from) {
  let name;
  if (_pathHistory.length == 0 && from.path) {
    _pathHistory.push(from.path);
    name = 'slide-left';
  } else {
    const lastIndex = _.lastIndexOf(_pathHistory, to.path);
    if (lastIndex !== -1) {
      // AA 
      _pathHistory = _pathHistory.slice(0, lastIndex + 1);
      name = 'slide-right';
    } else {
      // 
      _pathHistory.push(to.path);
      name = 'slide-left';
    }
  }
  return name;
}




